@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-400.woff2") format("woff2"), url("../fonts/montserrat-400.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("/source/fonts/montserrat-700.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.page {
  height: 100%;
}

.page__body {
  min-height: 100%;
  margin: 0;
  position: relative;
  font-family: "Montserrat", "Tahoma", sans-serif;
  color: #333333;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.header {
  background-color: #ffffff;
}

.header__wrapper {
  position: relative;
  max-width: 600px;
  margin: 0 auto;
  box-sizing: border-box;
}

.header__container {
  display: flex;
  padding: 0 20px;
  align-items: center;
  justify-content: space-between;
  margin-right: 42px;
}

.header__menu-button {
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}

.header__logo-link {
  display: block;
  background-image: url("../../img/logo-header-mobile.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 32px;
  height: 36px;
}

.header__logo-title {
  display: none;
}

.header__navbar {
  display: flex;
  align-items: center;
}

.navbar__item {
  padding: 22px;
  cursor: pointer;
}
.navbar__item--title {
  display: none;
}
.navbar__item:active {
  background-color: #f0ebff;
}
.navbar__item--image {
  display: block;
  fill: #333333;
}
.navbar__item--image:hover {
  fill: #7859cf;
}

@media (min-width: 768px) {
  .header__wrapper {
    max-width: 1200px;
  }

  .header__container {
    margin-right: 0;
  }

  .header__logo-link {
    background-image: url("../../img/logo-header-tablet.svg");
    width: 82px;
  }

  .navbar__item {
    padding: 32px 22px;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    max-width: 1600px;
  }

  .header__container {
    padding: 0 40px;
  }

  .header__logo-link {
    background-image: url("../../img/logo-header-desktop.svg");
    width: 170px;
    margin-right: 20px;
  }

  .header__logo--wrapper {
    display: flex;
    align-items: center;
  }

  .header__logo-title {
    display: inline-block;
    max-width: 130px;
    font-size: 12px;
    line-height: 16px;
    color: #333333;
  }

  .navbar__item {
    padding: 32px 20px;
    display: flex;
    align-items: center;
  }
  .navbar__item:hover {
    color: #7859cf;
  }
  .navbar__item--title {
    order: 2;
    display: inline-block;
    margin-left: 8px;
  }
  .navbar__item--user {
    margin-right: 20px;
  }

  .navbar__item:hover .navbar__item--image {
    fill: #7859cf;
  }
}
.promo__title {
