.filter {
  border: none;
  padding: 0;
  margin: 0;
  margin-bottom: 23px;
}

.filter__list {
  margin: 0;
  padding: 0;
}

.filter__item {
  list-style: none;
  margin: 0;
  margin-bottom: 20px;
}

.filter__title {
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  margin-bottom: 22px;
}

.radio__input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}

.radio__checkmark {
  position: relative;
  padding-left: 34px;
  padding-top: 4px;
  font-size: 16px;
  line-height: 24px;
}
.radio__checkmark::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
}

.radio__label,
.checkbox__label {
  cursor: pointer;
}

.radio__label:hover .radio__checkmark::before {
  border-color: #9070ec;
}

.radio__label:hover .radio__checkmark::after {
  display: block;
  background-color: rgba(120, 89, 207, 0.25);
}

.radio__checkmark::after {
  content: "";
  display: none;
  position: absolute;
  top: 6px;
  left: 6px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.radio__label input:checked ~ .radio__checkmark::after {
  display: block;
  background-color: #7859cf;
}

.radio__label input:disabled ~ .radio__checkmark::after {
  display: none;
}

.radio__label input:disabled:checked ~ .radio__checkmark::after {
  display: block;
  background-color: #e2e2e2;
}

.radio__label input:checked ~ .radio__checkmark::before {
  border-color: #7859cf;
}

.radio__label input:disabled ~ .radio__checkmark::before {
  border-color: #e2e2e2;
}

.radio__label input:disabled ~ .radio__checkmark {
  color: #e2e2e2;
}

.checkbox__input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}

.checkbox__checkmark {
  position: relative;
  padding-left: 54px;
}
.checkbox__checkmark::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border-radius: 12px;
}
.checkbox__checkmark::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
}

.checkbox__label input:checked ~ .checkbox__checkmark::after {
  left: 20px;
}

.checkbox__label input:checked ~ .checkbox__checkmark::before {
  background-color: #7859cf;
}

.checkbox__label input:disabled ~ .checkbox__checkmark::before {
  background-color: #e2e2e2;
  outline: none;
}

.checkbox__label input:disabled ~ .checkbox__checkmark {
  color: #e2e2e2;
}

.checkbox__label:hover .checkbox__checkmark::before {
  background-color: rgba(120, 89, 207, 0.25);
  outline: 2px solid #9070ec;
  outline-offset: -2px;
}

.checkbox__label:checked:disabled + .checkbox__checkmark::after {
  background-color: #bdbdbd;
}

.range-scale {
  position: relative;
  height: 4px;
  background-color: #ffffff;
  border: 1px solid #9070ec;
  margin-bottom: 35px;
}

.range-bar {
  position: absolute;
